.sidebar{
justify-items:center;
padding: 18px 15px 20px 15px;
grid-auto-rows:max-content;
@include responsive(tablet){
border-left: 1px solid #BBB;
display:grid;
grid-template-columns:1fr 1fr;
grid-auto-rows:max-content;
padding: 24px 15px 20px 15px;
border:none;
}

@include responsive(desktop){
border-left: 1px solid #BBB;
}

@include responsive(big-desktop){
padding: 24px 30px 20px 30px;
}

//width of container div
& > div{
margin-bottom:20px;
width:100%;
@include responsive(tablet){
width:90%;
}
@include responsive(desktop){
width:100%;
}
& > h2 {
padding-bottom: 10px;
width:100%;
text-align:center;
font-weight:500;
font-size:$med;
margin-bottom:10px;
@include responsive(tablet){
font-size:$large;
}
@include responsive(desktop){
font-size:$large;;
}
}//end h2

& > p > a, & > div > a {
color: $color-news;
}
& > p > a, & > div > a:hover {
color: $color-purple;
}
& > button, & > button:active, & > button:focus, & > button:hover, & > button:visited {
border-radius:0;
border:none;
text-decoration:none;
outline:none;
margin-top:3px;
}
& > button{
width:100%;
font-weight:$bold;
font-size:$big;
color:$color-dark;
border-radius:$rad-normal;
}
}//end div


& > nav{
& > a > .badge-secondary {
color: #847b79;
background-color: #eeedea;
}
& > a > .badge-secondary {
color: #847b79;
background-color: #eeedea;
}
& > a > .badge-pill{
padding-right: .6em;
padding-left: .6em;
border-radius: 10rem;
}
}//end nav


& > .about-us{
grid-column:1/3;
grid-row:1;
display:none;
flex-wrap:wrap;
justify-content:space-between;
align-content:space-between;
@include responsive(tablet){
display:flex;
grid-column:1/3;
grid-row:1;
}
@include responsive(desktop){
grid-column:1/3;
}

& > .well{
display:block;

& > .q-adjust{
//width:50%;
float:left;
font-size:$normal;
margin-top:10px;
}

& > img{
float:left;
margin:0px;
margin-top:28px;
margin-right:15px;
font-size:$tiny;
}

& > p{
vertical-align:top;
font-family: $font-news;
font-size: $normal;
color: $color-news;
//margin-top: 1rem;
margin-bottom:1rem;
}//end p

}//well block
}//end about us


& > .featured-posts{
grid-column:1/3;
grid-row:4;
display:flex;
flex-wrap:wrap;
justify-content:space-between;
align-content:space-between;
@include responsive(tablet){
grid-column:1/3;
grid-row:4;
}
@include responsive(desktop){
grid-column:1/3;
grid-row:2;
}
& > .preview-featured{
width:48%;
padding:$pad-zero;
margin:$margin-zero;
margin-bottom:40px;
margin:3px 0px 0px 3px;
display:flex;
justify-content:space-between;
align-content:space-between;
text-align:center;
@include responsive(phone){
width:45%;
}
@include responsive(tablet){
//width:30%;
}
@include responsive(desktop){
width:46%;
}
& > a {
width:100%;
font-size:$small;
height:auto;
padding:$pad-zero;
margin-bottom:10%;
@include responsive(phone){
font-size:$normal;
}
@include responsive(desktop){
font-size:$small;
}
& > div{
background-size: cover;
background-repeat: no-repeat;
background-position: center;
border-radius:$rad-normal;
height:100px;
margin-bottom:5px;
@include responsive(phone){
height:140px;
}
@include responsive(tablet){
height:120px;
}
@include responsive(big-tablet){
height:140px;
}
@include responsive(laptop){
height:150px;
}
@include responsive(desktop){
height:80px;
}
@include responsive(big-desktop){
height:100px;
}
}//end image
&:hover {
//@include background(none,$color-share-egg, .8, cover);
transform: scale(1.1);
}
&:visited {
//@include background(none,$color-green-7, .8, cover);
}
}//end a
}//end previews
}//end featured post

& > .recent-container{
grid-column:1/3;
grid-row:7;
display:flex;
flex-wrap:wrap;
justify-content:space-between;
align-content:space-between;
@include responsive(tablet){
grid-row:5;
}
@include responsive(desktop){
grid-row:3;
}
& > .previews{
width:48%;
padding:$pad-zero;
margin:$margin-zero;
margin-bottom:40px;
margin:3px 0px 0px 3px;
display:flex;
justify-content:space-between;
align-content:space-between;
text-align:center;
@include responsive(phone){
width:45%;
}
@include responsive(tablet){
//width:30%;
}
@include responsive(desktop){
width:46%;
}
& > a {
width:100%;
font-size:$small;
height:auto;
padding:$pad-zero;
margin-bottom:10%;
@include responsive(phone){
font-size:$normal;
}
@include responsive(desktop){
font-size:$small;
}
& > div{
background-size: cover;
background-repeat: no-repeat;
background-position: center;
border-radius:$rad-normal;
height:100px;
margin-bottom:5px;
@include responsive(phone){
height:140px;
}
@include responsive(tablet){
height:120px;
}
@include responsive(big-tablet){
height:140px;
}
@include responsive(laptop){
height:150px;
}
@include responsive(desktop){
height:80px;
}
@include responsive(big-desktop){
height:100px;
}
}//end image
&:hover {
//@include background(none,$color-share-egg, .8, cover);
transform: scale(1.1);
}
&:visited {
//@include background(none,$color-green-7, .8, cover);
}
}//end a
}//end previews
}//end more recent posts

//end ads
}//end sidebar
