//mixins
@mixin text-template($size, $family,$color) {
font-size:$size;
font-family:$family;
//border-radius:.625rem;
color:$color;
//padding: 0px 2px 20px 2px !important;
} //end text-template


@mixin background($img, $color, $screen, $size ) {
background-image: linear-gradient(to right bottom, rgba($color, $screen), rgba($color, $screen)), url($img);
background-size: $size;
background-repeat:no-repeat;
background-position:center center;
} //end background mixin

@mixin transition{
transition: all .2s ease-in-out;
}

@mixin darken($baseColor,$per){
background-color: darken($baseColor, $per)}

@mixin icons{
margin-right:20px ;
font-size: 15px;
color: #57D131;
//background-color: #a1ce4f;
color:#a4d155;
color:#008000ff;
speak: none;
font-style: normal;
font-weight: normal;
font-variant: normal;
text-transform: none;
line-height: 1;
/* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


@mixin background($img, $color, $screen, $size ) {
background-image: linear-gradient(to right bottom, rgba($color, $screen), rgba($color, $screen)), url($img);
background-size: $size;
background-repeat:no-repeat;
background-position:center center;
} //end background mixin

@mixin background-double($img1,$img2, $color, $screen, $size1, $size2) {
background-image: linear-gradient(to right bottom, rgba($color, $screen), rgba($color, $screen)), url($img1),url($img2);
background-size: $size1, $size2;
background-repeat:no-repeat, no-repeat;
background-position:center center, center center;
} //end double-background mixin

@mixin transition{
transition: all .2s ease-in-out;
}


