$scss:"yes";
.ads{
justify-items:center;
padding: 18px 15px 20px 15px;
grid-auto-rows:max-content;

@include responsive(tablet){
display:grid;
grid-template-columns:1fr 1fr;
grid-auto-rows:max-content;
padding: 24px 15px 20px 15px;
border:none;
}

@include responsive(desktop){
border-right: 1px solid #BBB;
}
@include responsive(big-desktop){
padding: 24px 30px 20px 30px;
}

//width of container div
& > div{
width:100%;
@include responsive(tablet){
width:90%;
}
@include responsive(desktop){
width:100%;
}
& > h2 {
padding-bottom: 10px;
width:100%;
text-align:center;
font-size:$med;
@include responsive(tablet){
font-size:$large;
}
@include responsive(desktop){
font-size:$large;
}
& > .side-icon{
font-size:$big;
color:$color-news;
margin-left:8px;
@include responsive(tablet){
font-size:$large;
}
}
}//end h2
& > div{
& > h6 {
margin-top:10px;
text-align:center;
}
}
& > button, & > button:active, & > button:focus, & > button:hover, & > button:visited {
border-radius:0;
border:none;
text-decoration:none;
outline:none;
margin-top:3px;
}
& > button{
width:100%;
font-weight:$norm;
font-size:$big;
color:$color-dark;
border-radius:$rad-normal;
@include responsive(tablet){
font-size:$normal-plus;
}
@include responsive(desktop){
font-size:$less-big;
}
@include responsive(big-desktop){
font-size:$big;
}
}
}//end div

& > nav{
& > a > .badge-secondary {
color: #847b79;
background-color: #eeedea;
}
& > a > .badge-secondary {
color: #847b79;
background-color: #eeedea;
}
& > a > .badge-pill{
padding-right: .6em;
padding-left: .6em;
border-radius: 10rem;
}
}//end nav

& > .vimeo{
grid-column:1/3;
grid-row:1;
margin-bottom:25px;
@include responsive(tablet){
grid-column:1/3;
//display:none;
}
@include responsive(desktop){
grid-column:1/3;
display:grid;
}

& > #vimeoVids{
& > div > iframe{
border-radius:$rad-normal;
}
}//end vimeo vids

& > #vimeoSelector{
@include background(none,$color-vimeo, .8, cover);
&:hover {
@include background(none,$color-share-egg, .8, cover);
}
&:visited {
@include background(none,$color-vimeo, .8, cover);
}
}// end vimeoSelector
}//end vimeo


& > .youtube{
grid-column:1/3;
grid-row:4;

@include responsive(tablet){
grid-column:1/3;
grid-row:4;
//display:none;
}

@include responsive(desktop){
display:grid;
grid-column:1/3;
grid-row:4;
}

& > h2 > p {
margin:8px 0px 3px 0px;
}


& > #youtubeVids{
width:100%;
& > div > iframe{
border-radius:$rad-normal;
}
}//end youtubevids

& > #youtubeSelector{
@include background(none,$color-green-7, .8, cover);
&:hover {
@include background(none,$color-share-egg, .8, cover);
}
&:visited {
@include background(none,$color-green-7, .8, cover);
}
}//end youtube selector
}//end youtube





& > a:hover {
color: #23527c;
background-color: #eee;
}



& > .anchor-vimeo {
//margin-top:100rem;
}

& > .recent-container{
grid-column:1/3;
grid-row:7;
display:flex;
flex-wrap:wrap;
justify-content:space-between;
align-content:space-between;
display:none;

@include responsive(tablet){
grid-row:4;
}
@include responsive(desktop){
grid-row:7;
}
& > .previews{
width:48%;
padding:$pad-zero;
margin:$margin-zero;
margin-bottom:40px;
margin:3px 0px 0px 3px;
//height:150px;
display:flex;
justify-content:space-between;
align-content:space-between;
text-align:center;
//flex-wrap:wrap;
@include responsive(phone){
width:45%;
}
@include responsive(tablet){
width:30%;
}
@include responsive(desktop){
width:46%;
}
& > a {
width:100%;
font-size:$small;
height:auto;
padding:$pad-zero;
margin-bottom:10%;
@include responsive(phone){
font-size:$normal;
}
@include responsive(desktop){
font-size:$small;
}
& > div{
bakground-size: cover;
background-repeat: no-repeat;
background-position: center;
border-radius:$rad-normal;
height:100px;
margin-bottom:5px;
@include responsive(phone){
height:140px;
}
@include responsive(tablet){
height:120px;
}
@include responsive(big-tablet){
height:140px;
}
@include responsive(laptop){
height:150px;
}
@include responsive(desktop){
height:80px;
}
@include responsive(big-desktop){
height:100px;
}
}//end image
&:hover {
@include background(none,$color-share-egg, .8, cover);
transform: scale(1.1);
}
&:visited {
@include background(none,$color-green-7, .8, cover);
}
}//end a
}//end previews
}//end recent header
}//end ads
