$scss: "yes";
.main{

//recent posts
.posts {
width:100%;
padding:0px 15px 0px 15px;
margin-top:15px;
margin-bottom:15px;

&  a.title, &  a.title:hover{
color: #222;
text-decoration: none;
}//title link
}//end posts

.post {

}

& div{
& > ul{

& > .page-item.active > .page-link {
z-index: 1;
color: #fff;
background-color: rgba(39, 52, 60, 0.9);
border-color: transparent;
}

& > .page-item.disabled > .page-link {
color: #515151;
pointer-events: none;
cursor: auto;
background-color: #fff;
border-color: #dee2e6;
}

& > li{
& > .page-link {
color: #515151;
}

}//end li
}//end ul
}//end div

}//end main
