.feature-image {
background: no-repeat center center;
display: flex;
align-items: center;
color: $color-feature-text;
@include responsive(small-phone) {
min-height: 240px;
}
@include responsive(phone) {
min-height: 260px;
}
@include responsive(tablet) {
min-height: 330px;
}
@include responsive(big-tablet) {
min-height: 380px;
}
@include responsive(desktop) {
min-height: 600px;
}
//nested div
& > .feature-image-text {
max-width: 760px;
margin-top:20px;
margin-right: auto;
margin-left: auto;
//color: $color-feature-text;
@include responsive(small-phone) {
max-width: 80%;
margin-top:0.3125px;
}
@include responsive(phone) {
max-width: 80%;
margin-top:0.625rem;
}
@include responsive(big-tablet) {
max-width: 37.5rem;
margin-top:0.9375rem;
}
@include responsive(desktop) {
max-width: 47.5rem;
margin-top:1.25rem;
}

& > h1 {
@include responsive(small-phone) {
font-size: $big;
}
@include responsive(phone) {
font-size: $large;
}
@include responsive(tablet) {
font-size: $large;
}
@include responsive(big-tablet) {
font-size: $jumbo;
}
@include responsive(desktop) {
font-size: $massive;
}
}//end feature-image-text h1

& > .intro-description {
margin-top:4px;
font-size: $large;
text-align:center;
@include responsive(small-phone) {
font-size: $normal;
}
@include responsive(phone) {
font-size: $normal;
}
@include responsive(tablet) {
font-size: $normal-plus;
}
@include responsive(big-tablet) {
font-size: $big;
}
@include responsive(desktop) {
font-size: $large;
}
}//end feature-imate-text intro-description

}//end feature-image-text
}//end header feature-image
